/************** RecentlyAdded Page **************/

#recentPage {
    width: 100%;
    max-width: var(--max-width);
    min-width: 320px;
    margin: auto;
    transition: bottom 250ms, opacity 250ms;
  }
  #recentHeader {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: var(--main-color);
  }
  #recentHeaderTitle {
    color: white;
    margin: 0;
    margin-right: 30px;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
  }
  #recentBack {
    margin-left: 20px;
    width: 35px;
    height: 35px;
    fill: white;
    cursor: pointer;
  }
  #recentBack:hover {
    fill: var(--text-kinda-black);
  }
  #recentSelectMeal {
    display: block;
    width: 87%;
    height: 40px;
    margin: auto;
    margin-top: 35px;
    margin-bottom: 35px;
    color: var(--text-kinda-black);
    font-size: 18px;
    font-weight: bold;
    background-color: var(--background-gray);
    border: none;
    border-radius: 0;
    border-bottom: solid 1px var(--text-kinda-black);
  }
  #recentDateText {
    display: inline-block;
    direction: ltr;
    text-align: right;
    margin: 0;
    padding: 5px 25px 5px 0;
    color: var(--text-gray);
    font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
  }
  .recentAddAll {
    float: left;
    text-align: right;
    margin: 0;
    padding: 5px 0px 5px 25px;
    background-color: rgba(0, 0, 0, 0);
    font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    color: var(--main-color);
    border: none;
    cursor: pointer;
  }
  .recentAddAll:hover {
    color: var(--second-color);
  }
  #recentNoResults {
    text-align: center;
    margin-top: 150px;
    color: var(--text-gray);
  }
  @media (max-width: 450px) {
    #recentBack {
      width: 30px;
      height: 30px;
    }
  }
  